* {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
}

body {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  background: linear-gradient(#9ffde7, #fae19d);
  display: flex;
  justify-content: center;
  align-items: center;
}

h1 {
  text-align: center;
  margin-bottom: 1em;
}

.container {
  height: 450px;
  width: 450px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 1em;
}

.box {
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  transform: rotateY(180deg);
  transition: transform 0.5s ease-in-out;
}

@keyframes hideCard {
  0%,
  70% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(180deg);
  }
}

.box img {
  max-width: 80%;
  max-height: 80%;
  transition: transform 0.5s;
  transform: scale(0);
}
@keyframes hideImage {
  0%,
  70% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

.box.active {
  transform: rotateY(0);
}
.box.correct {
  background-color: #65e469;
}
.box.wrong {
  background-color: #fd245a;
}
.box.active img {
  transform: scale(1);
}

.App h1 {
  font-size: 3rem;
}

.App p {
  font-size: 1.5rem;
  padding: 0.6rem;
  margin: auto;
}

.box:hover {
  scale: 105%;
}

@media (max-width: 360px) and (max-height: 700px) {
  .container {
    height: 250px;
    width: 250px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    gap: 0.3em;
    margin: auto;
  }
}
